// Container Class
.container {
    max-width: 1200px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    padding-left: 15px;
    padding-right: 15px;
}

.img-fluid {
    width: 100%;
    object-fit: contain;
    image-rendering: optimizeQuality;
    user-select: none;
}

.text-muted {
    color: #777;
    font-size: 13px;
}