//*? Flex Layout
@mixin flex_layout($flex) {
    @if ($flex == "centering") {
        display: flex;
        justify-content: center;
        align-items: center;
    }
    @else if ($flex == "start") {
        display: flex;
        justify-content: flex-start;
        align-items: center;
    }
    @else if ($flex == "end") {
        display: flex;
        justify-content: flex-end;
        align-items: center;
    }
    @else if ($flex == "space-between") {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    @else if ($flex == "space-around") {
        display: flex;
        justify-content: space-around;
        align-items: center;
    }
    @else if ($flex == "space-evenly") {
        display: flex;
        justify-content: space-evenly;
        align-items: center;
    }
    @else if ($flex == "self-start") {
        align-self: flex-start;
    }
    @else if ($flex == "self-center") {
        align-self: center;
    }
    @else if ($flex == "self-end") {
        align-self: flex-end;
    }
    @else if ($flex == "centering-content") {
        display: flex;
        justify-content: center;
        align-content: center;
    }
}

//*? Grid Layout
@mixin grid_layout($auto, $min) {
    @if ($auto == "fit") {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax($min, 1fr));
    }
    @if ($auto == "fill") {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax($min, 1fr));
    }
};