//*? Import Sass Files
@import "../mixins/borders";
@import "../mixins/buttons";
@import "../mixins/breakpoints";
@import "../mixins/layout";
@import "../mixins/transition";
//!=====================================================================
.product-btns {
    width: fit-content;
    margin: 0 auto;
    height: 30px;
    white-space: nowrap;
    > * {height: 100%; vertical-align: middle;}
    .btn {
        width: 25px;
        font-weight: bolder; font-size: 1rem;
        background: none;
        border: none;
        border: 1px solid #ccc;
        cursor: pointer;
        transition: .3s;
        &:hover {border: 1px solid transparent}
        &:hover {background-color: $primary-color; color: #fff;}
    }
    .quantity {
        display: inline-block;
        width: 50px; text-align: center;
        border-top: 1px solid #ccc;
        border-bottom: 1px solid #ccc;
    }
}
//!=====================================================================
#cart_products {
    padding: 60px 0;
    .shopping-link {
        .products-link {
            color: $primary-color;
            font-weight: 500;
            &::before {
                content: "\f30a";
                font: {
                    family: "font awesome 5 free";
                    weight: 900;
                }
                transition: $main-trans;
                margin-right: .5rem;
            }
            &:hover:any-link::before {
                margin-right: .8rem;
            }
        }
    }
    .products-container {
        .products-table {
            overflow-x: auto;
            &::-webkit-scrollbar {
                height: .25rem;
            }
            &::-webkit-scrollbar-track {
                background-color: #eee;
                border-radius: .5rem;
                box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2) inset;
            }
            &::-webkit-scrollbar-thumb {
                border-radius: .5rem;
                background-color: $primary-color;
            }
            .table {
                border-collapse: collapse;
                width: 100%;
                thead {
                    tr {
                        @include border("bottom", 1px, #e1e1e1);
                        th {
                            padding: 10px;
                        }
                    }
                }

                tbody {
                    .product-row {
                        white-space: nowrap;
                        &:not(:last-child) {@include border("bottom", 1px, #e1e1e1);}
                        &:not(:nth-of-type(even)) {background-color: #fafafa;}
                        td {
                            text-align: center;
                            padding: .5rem 1rem;
                            font-weight: 500;
                            .pro-img {
                                width: 50px; height: 50px;
                                object-fit: contain;
                                display: block;
                                margin:  0 auto;
                            }
                            &:nth-child(2) {
                                color: $black;
                            }
                            &:nth-child(3) {
                                color: $primary-color;
                            }
                            &:nth-child(4) {
                                color: #777;
                            }
    
                            .remove {
                                @include custom_btn(30px, 30px);
                                background: none;
                                filter: drop-shadow(2px 2px 2px rgba(0, 0, 0, 0.3));
                                svg {color: $red; font-size: 1.25rem; pointer-events: none;}
                            }
                        }
                    }
                }
            }
    
        }
        .cart-products-footer {
            @include border("top", 1px, $light);
            display: flex; justify-content: space-between;
            gap: 1.5rem;
            margin-top: 2rem;
            padding-top: 1.5rem;
            .clear-cart {
                border: 1px solid;
                @include custom_btn(150px, 40px);
                background-color: $red; color: $white;
                font-size: 1rem; text-transform: capitalize;
                border-radius: 3px;
            }
    
            .cart-total-amount {
                padding: 5px;
                .content {
                    .total-price {
                        @include flex_layout("space-between");
                        span {
                            font: {
                                weight: 500;
                                size: 18px;
                            }
                        }
    
                        strong {
                            font-size: 1.25rem;
                        }
                    }
                    .checkout {
                        display: block;
                        background-color: $primary-color;
                        color: $white; text-align: center;
                        padding: 8px 0; margin: 10px 0 1rem;
                        border-radius: 3px;
                    }
                }
            }
            @media (max-width: 600px) {
                justify-content: flex-start;
                flex-direction: column-reverse;
                .cart-total-amount {@include border("bottom", 1px, $light); padding-bottom: 1rem;}
                .clear-cart {width: 100%;}
            }
        }
    }

    .empty-cart {
        text-align: center;
        height: 300px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        .empty-cart-msg {
            h3 {color: $black;}
            svg {
                margin: 1rem 0;
                color: $light;
            }
        }
    }
}
