    @import "../global-rules";
@import "../classes";
@import "../variables";
//*? Import Sass Mixins
@import "../mixins/borders";
@import "../mixins/buttons";
@import "../mixins/breakpoints";
@import "../mixins/layout";
@import "../mixins/transition";
//!==================================================================
#product_details {
    padding: 60px 0;
    .row {
        display: flex;
        justify-content: center;
        flex-flow: wrap;
        row-gap: 2rem;
        .product-img {
            margin-right: 2rem;
            width: 350px;
            img {
                max-width: 250px;
                object-fit: contain;
                image-rendering: optimizeQuality;
                display: table;
                margin: 0 auto;
                @media (max-width: 500px) {
                    max-width: 200px;
                }
            }
        }

        .product-info {
            flex: 1;
            .navigate {
                display: flex; align-items: center;
                margin-bottom: 1.5rem;
                li {
                    color: $black;
                    font-weight: 400;
                    &:first-child::after {
                        content: "/";
                        padding: 0 6px;
                    }
                    a {
                        color: $primary-color;
                        text-decoration: underline;
                    }
                }
            }

            .details-content {
                .title {
                    color: $black;
                }
                .descrip {
                    max-width: 600px;
                    margin: 10px 0;
                }
                .price {
                    color: $primary-color;
                    font-weight: bolder;
                }
            }

            .product-btns {
                margin: 15px 0;
            }

            .add {
                @include custom_btn(150px, 40px);
                margin-top: 1rem;
                background-color: $black;
                color: $white;
                text-transform: capitalize;
                border-radius: 3px;
                transition: .3s;
                &:hover {
                    background-color: $primary-color;
                }
            }
        }
        @media (max-width: 767px) {
            > * {width: 100%;}
            .product-img {
                width: 100%;
                margin-right: 0;
                img {margin: 0 auto;}
            }
        }
    }
}