//*? Import Sass Files
@import "../global-rules";
@import "../classes";
@import "../variables";
//*? #Mixins
@import "../mixins/borders";
@import "../mixins/buttons";
@import "../mixins/breakpoints";
@import "../mixins//layout";
@import "../mixins/transition";

.landing-page {
    min-height: calc(40vh + 67px);
    background-color: #eee;
    display: grid; place-items: center;
    h1 {
        text-align: center;
        padding-top: calc(30px * 2);
    }

    @media (max-width: 767px) {
        min-height: calc(30vh + 67px);
    }
}
//!=====================================================================
//* Our Products
#our_products {
    padding: 60px 0;
    background-color: $white;
    .sec-title {
        width: fit-content;
        margin: 0 auto 2rem;
        position: relative;
        &::after, &::before {
            content: "";
            position: absolute;
        }
        &::after {
            width: 100%; bottom: -.5rem;
            @include border("bottom", 2px, $primary-color);
            left: 0;
        }
        &::before {
            width: 10px; height: 10px;
            @include border("all", 2px, $primary-color);
            bottom: -14px; left: 50%;
            transform: translateX(-50%) rotate(45deg);
            background-color: $white;
            z-index: 1;
        }
    }
    .row {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
        gap: 1.5rem;
        .card {
            box-shadow: 0px 0px 4px 0 rgba(0, 0, 0, 0.4);
            border-radius: $main-radius $main-radius 0 0;
            .card-img {
                @include border("bottom", 1px, $light);
                padding: 1rem 0;
                .img-fluid {
                    display: table;
                    height: 180px; width: 200px;
                    margin: 0 auto;
                    @include trans(.6s, linear);
                }
            }
            .card-content {
                padding: 1rem 10px;
                .title {
                    color: $black;
                    margin-bottom: 10px;
                }
                .price {
                    display: inline-block;
                    margin: 10px 0 1rem;
                }

                .card-btns {
                    .btn {
                        display: inline-block;
                        @include custom_btn(100px, 30px);
                        text-align: center; line-height: 30px;
                        font-size: 13px;
                        color: $white; text-transform: capitalize;
                        border-radius: 2px;
                        transition: $main-trans;
                        &:hover:any-link {
                            opacity: .8;
                        }
                        &.add {
                            margin-right: 10px;
                            background-color: $primary-color;
                        }
                        &.detalis {
                            background-color: $black;
                        }
                    }
                }
            }
            &:hover {
                .img-fluid {
                    transform: scale(.9);
                }
            }
        }
    }
}
//!=====================================================================
//* Not Found Page
.not-found {
    position: absolute;
    width: 100%; height: 100%;
    @include flex_layout("centering");
    flex-direction: column;
    font-weight: bolder;
    h2 {
        color: $red;
        font-size: 3rem;
    }
    p {
        font: {
            size: 1.25rem;
        }
    }
}
//!=====================================================================